<template>
  <section class="row">
    <div class="card border-dark paperDarkBg">
      <div class="card-body d-flex align-items-start text-uppercase">
        <span class="h4 me-3">step 1</span
        ><span class="h3" style="font-family: BellGothicStd">Select</span>
      </div>
    </div>
    <!-- 左 -->
    <div class="card border-dark col-sm-6 px-0">
      <div class="card-body px-0">
        <Information />
      </div>
    </div>
    <!-- 右 -->
    <div class="card border-dark col-sm-6">
      <div class="card-body">
        <Options :productList="productList" @addToCart="addToCart" />
      </div>
    </div>
  </section>
  <!-- 下 -->
  <section class="row">
    <div class="card border-dark paperDarkBg">
      <div class="card-body d-flex align-items-start text-uppercase">
        <span class="h4 me-3">step 2</span
        ><span class="h3" style="font-family: BellGothicStd">Your Cart</span>
      </div>
    </div>
    <div class="card p-0 border-0">
      <div class="card-body p-0">
        <Chartlist :cookieData="cartList" :productList="productList" />
      </div>
    </div>
  </section>
</template>

<script>
import Cookies from 'js-cookie';
import { apiGetProducts } from '@/assets/javascript/api';
import Options from '@/components/shop/ShoppingOptionsModel.vue';
import Information from '@/components/shop/ProductInformationModel.vue';
import Chartlist from '@/components/shop/CartListModel.vue';

export default {
  data() {
    return {
      cartList: [],
      productList: [],
      tempCookie: '',
    };
  },
  components: {
    Options,
    Information,
    Chartlist,
  },
  methods: {
    forceRerender() {
      this.componentKey += 1;
    },
    getProducts() {
      this.$emitter.emit('loadingStatus', true);
      apiGetProducts()
        .then((res) => {
          this.productList = res.data.data;
        })
        .catch((err) => {
          console.log(err);
        });
      this.$emitter.emit('loadingStatus', false);
    },
    addToCart(product) {
      const optionExist = this.cartList.findIndex(
        (element) => element.optionId === product.optionId
          && element.ice === product.ice
          && element.sugar === product.sugar
          && element.size === product.size,
      );
      if (Cookies.get('LULUchartList') === undefined) {
        Cookies.set(
          'LULUchartList',
          JSON.stringify({
            productId: product.productId,
            optionId: product.optionId,
            ice: product.ice,
            sugar: product.sugar,
            quantity: product.quantity,
            flavor: product.flavor,
            size: product.size,
            price: product.price,
            type: product.type,
            name: product.flavor.split('.')[1],
          }),
          {
            expires: 7,
          },
        );
      } else if (optionExist > -1) {
        this.tempCookie = '';
        this.cartList[optionExist].quantity += product.quantity;
        this.cartList.forEach((item) => {
          this.tempCookie += `${JSON.stringify(item)};`;
        });
        this.tempCookie = this.tempCookie.replace(/.$/, '');
        Cookies.set('LULUchartList', this.tempCookie, {
          expires: 7,
        });
      } else {
        let currentItem = Cookies.get('LULUchartList');
        currentItem = `${currentItem}; ${JSON.stringify({
          productId: product.productId,
          optionId: product.optionId,
          ice: product.ice,
          sugar: product.sugar,
          quantity: product.quantity,
          flavor: product.flavor,
          size: product.size,
          price: product.price,
          type: product.type,
          name: product.flavor.split('.')[1],
        })}`;
        Cookies.set('LULUchartList', currentItem, {
          expires: 7,
        });
      }
      this.getCookie();
    },
    getCookie() {
      if (Cookies.get('LULUchartList')) {
        this.cartList = [];
        const tempCookie = Cookies.get('LULUchartList').split(';');
        Object.values(tempCookie).forEach((item) => {
          this.cartList.push(JSON.parse(item));
        });
      }
    },
  },
  mounted() {
    this.getProducts();
    this.getCookie();
    this.$emitter.on('getCookie', () => {
      this.getCookie();
    });
  },
};
</script>
